.list {
  margin-left: auto;
  margin-right: auto;
  padding-left: 0;
}
.item {
  text-align: left;
  margin-bottom: 15px;
  padding: 15px;

  font-size: 25px;
  font-weight: 600;
  color: #29628b;
}
.name {
  margin-right: 15px;
}
.button__delete {
  padding: 0 15px;
  margin-left: auto;
  cursor: pointer;
  text-align: center;
  justify-content: center;

  font-size: 15px;
  font-family: 'Dancing Script', cursive;
  font-weight: 500;
  color: #fcfcfa;

  background-color: #337bae;
  border-radius: 15px;
  border: none;
}
.button__delete:hover {
  background-color: #1a405f;
}
.table {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}
.table__date {
  padding: 15px;
  text-align: left;
}
